$signatureColor: #04aaff;

.InsertForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
  .InsertInput__container {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    margin-bottom: 20px;
    .InsertInput__input {
      flex: auto;
      height: 40px;
      border: 1px solid $signatureColor;
      padding: 0px 20px;
      color: #fff;
      border-radius: 20px;
      font-weight: 500;
      font-size: 12px;
      &::placeholder {
        color: #fff;
        opacity: 0.9;
      }
    }
    .InsertInput__arrow {
      position: absolute;
      right: 0;
      width: 40px;
      height: 40px;
      border-radius: 20px;
      background-color: $signatureColor;
      padding: 10px 0px;
      text-align: center;
      color: #fff; 
    }
  }
  .InsertInput__label {
    color: $signatureColor;
    cursor: pointer;
    span {
      margin-right: 10px;
      font-size: 12px;
    }
  }
  .insertform__attachment {
    display: flex;
    flex-direction: column;
    justify-content: center;
    img {
      width: 80px;
      height: 80px;
      border-radius: 40px;
    }
    .Insertform__clear {
      color: $signatureColor;
      cursor: pointer;
      text-align: center;
      span {
        margin-right: 10px;
        font-size: 12px;
      }
    }
  }
}