// index.scss
@import url("https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css");
* {
  margin: 0;
  padding: 0; 
  box-sizing: border-box;
}
ul {
  list-style: none;
}
input {
  all: unset;
  box-sizing: border-box;
  appearance: none;
}
body {
  background-color: #000;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 14px;
  color: #fff;
}
button {
  background-color: #fff;
  color: #000;
}
a {
  text-decoration: none; 
  color: inherit;
}
form {
  width: 100%;
}

.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  margin-top: 80px;
}

.formInput {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid #000;
  background-color: #fff;
  text-align: center;
  color: #000;
}
.formBtn {
  width: 100%;
  padding: 7px 20px;
  background-color: #04aaff;
  color: #fff;
  border-radius: 20px;
  text-align: center;
  cursor: pointer;
}
.cancelBtn {
  background-color: tomato;
  cursor: pointer;
}